@import "months/style.css";
:root {
  --color-1: #124e78ff;
  --color-2: #f0f0c9ff;
  --color-3: #f2bb05ff;
  --color-4: #040107ff;
  --color-5: #6e0e0aff;
}

.hidden {
  display: none !important;
}

body {
  color: var(--color-1);
  font-family: "Poppins", sans-serif;
  margin: 0;
  background: var(--color-1);
  overflow-x: hidden;
}
body main {
  width: fit-content;
}
body main h1 {
  text-align: center;
  margin: 0;
  font-size: 5rem;
}
body .scroll-elem {
  height: 100vh;
  width: 100vw;
  position: relative;
  z-index: 2;
}
body .scroll-elem section {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
body .scroll-elem section[data-page="1"] {
  background: var(--color-1);
}
body .scroll-elem section[data-page="2"] {
  background: var(--color-2);
}
body .scroll-elem section[data-page="3"] {
  background: var(--color-3);
}
body .scroll-elem section[data-page="4"] {
  background: var(--color-4);
}
body .scroll-elem section[data-page="5"] {
  background: var(--color-5);
}
body .scroll-elem section[data-page="6"] {
  background: var(--color-6);
}
body .scroll-elem section[data-page="7"] {
  background: var(--color-7);
}
body .scroll-elem section[data-page="8"] {
  background: var(--color-8);
}
body .scroll-elem section[data-page="9"] {
  background: var(--color-9);
}
body .scroll-elem section[data-page="10"] {
  background: var(--color-10);
}
body .scroll-elem section[data-page="11"] {
  background: var(--color-11);
}
body .scroll-elem section[data-page="12"] {
  background: var(--color-12);
}
body .scroll-elem section[data-page="13"] {
  background: var(--color-13);
}
body .scroll-elem section[data-page="14"] {
  background: var(--color-14);
}
body .scroll-elem section[data-page="15"] {
  background: var(--color-15);
}
body .scroll-elem section[data-page="3"] {
  translate: 100% 0%;
}
body .scroll-elem section[data-page="4"] {
  translate: 0% 100%;
  overflow: hidden;
  position: relative;
}
body .scroll-elem section[data-page="5"] {
  translate: -50% 100%;
}
body .scroll-elem section[data-page="10"] {
  position: relative;
  overflow: hidden;
  translate: 0 100%;
}
body .scroll-elem section[data-page="10"] img {
  position: absolute;
  object-fit: cover;
  height: 100%;
  width: 100%;
}
body .scroll-elem section[data-page="10"] img:nth-child(1) {
  scale: 4;
  opacity: 0;
  filter: blur(40pt);
}
body .scroll-elem section[data-page="10"] img:nth-child(2) {
  scale: 4;
  opacity: 0;
  filter: blur(40pt);
}
body .scroll-elem section[data-page="10"] img:nth-child(3) {
  scale: 4;
  opacity: 0;
  filter: blur(40pt);
}
body .scroll-elem section[data-page="10"] img:nth-child(4) {
  scale: 4;
  opacity: 0;
  filter: blur(40pt);
}
body .scroll-elem section main {
  display: grid;
  grid-template-rows: repeat(2, auto);
  height: 40%;
  width: 40%;
  pointer-events: none;
}
body .scroll-elem section main div {
  position: relative;
}
body .scroll-elem section main div .landing-text {
  position: absolute;
  left: 50%;
  top: 50%;
  translate: -50% -50%;
}
body .scroll-elem section main div .landing-text.post {
  z-index: 1;
}
body .scroll-elem section main div #upperTextPost {
  filter: blur(40pt);
  opacity: 0;
}
body .scroll-elem section main div #lowerTextPost {
  mix-blend-mode: lighten;
}

section[data-page="4"] {
  pointer-events: none;
}
section[data-page="4"] div[data-month~=""] {
  position: absolute;
}